
import { TiLocale } from '@cloud/tiny3';
import { ctcen_US, ctczh_CN, currencyCNY, dateHyphen } from '@cloud/tinyplus3/locale/i18n';

import { siteConfig } from './siteConfig';

export class TpConfig {
    public static init(language: string = 'zh-CN'): void {
        const words: any = TiLocale.getWords();
        // 设置所有语言包
        TiLocale.setWords({
            'zh-CN': { ...words['zh-CN'], ...ctczh_CN, ...dateHyphen, ...currencyCNY, ...siteConfig },
            'en-US': { ...words['en-US'], ...ctcen_US, ...dateHyphen, ...currencyCNY, ...siteConfig }
        });
        // 设置默认语言
        TiLocale.setLocale(language);
    }
}
